___CSS_LOADER_EXPORT___.push([module.id, `.editproduct-container {
  padding: 2rem;
  background: #f9f9f9;
  min-height: 100vh;
  text-align: center;
}

.editproduct-form {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
  text-align: left;
}

.editproduct-form input,
.editproduct-form textarea {
  width: 100%;
  margin-bottom: 1rem;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.editproduct-form label {
  font-size: 1rem;
  color: #555;
  display: block;
  margin-bottom: 1rem;
}

.button-group {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.update-button,
.delete-button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.update-button {
  background: #007bff;
  color: white;
}

.update-button:hover {
  background: #0056b3;
}

.delete-button {
  background: #dc3545;
  color: white;
}

.delete-button:hover {
  background: #c82333;
}
